import { ChangeEvent, CSSProperties, FC } from "react";
import { Card, CardContent, FormControl } from "@material-ui/core";
import classes from "./QuickReply.module.css";
import { Close } from "@mui/icons-material";

import { CustomTextFieldFilled } from "../../../../UI/CustomTextField/CustomTextField";

interface QuickReplyCardProps {
  closeCard?: () => void;
  cardTitle?: string | "Untitled";
  value: string;
  onChangeValue?: (
    event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => void;
  style?: CSSProperties;
}

interface CardHeaderProps {
  cardTitle: string;
  closeCard: () => void;
}

const CardHeader: FC<CardHeaderProps> = ({ cardTitle, closeCard }) => {
  return (
    <div className={classes.Header}>
      <h1>{cardTitle || "Card Title"}</h1>

      <div className={classes.Card_Control} onClick={closeCard}>
        <Close />
      </div>
    </div>
  );
};

const QuickReplyCard: FC<QuickReplyCardProps> = ({
  closeCard,
  cardTitle,
  style,
  onChangeValue,
  value,
}) => {
  return (
    <Card variant="outlined" className={classes.ItemCard} style={style}>
      <CardContent>
        <CardHeader closeCard={closeCard} cardTitle={cardTitle} />

        {/* Select  Item*/}
        <FormControl variant="filled" className={classes.ItemSelect}>
          <CustomTextFieldFilled
            id="filled"
            label={"Label"}
            onChange={onChangeValue}
            variant="filled"
            value={value}
            name="quickLabel"
            required
          />
        </FormControl>
      </CardContent>
    </Card>
  );
};

export default QuickReplyCard;
