import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 7,
    margin: -7,
  },

  thumb: {
    width: 24,
    height: 24,
    backgroundColor: "#fff",
    boxShadow: "#000",
  },

  switchBase: {
    color: "#000 !important",
    padding: 7,
  },

  track: {
    borderRadius: 20,
    backgroundColor: "#000 !important",
  },

  checked: {
    "& $thumb": {
      backgroundColor: "#fff !important",
    },
    "& + $track": {
      opacity: "1 !important",
      backgroundColor: "#32C5FF !important",
    },
  },

  focusVisible: {},

  overlay: {
    position: "fixed",
    width: "100%",
    height: "100%",
    left: "0px",
    top: "0px",
    backgroundColor: "#00000080",
    zIndex: 9,
  },

  "template-preview": {
    backgroundColor: "#FFFFFF",
    boxShadow: "0px 0px 12px 1px rgba(0, 0, 0, 0.25)",
    display: "flex",
    flexDirection: "column",
    height: "100%",
    position: "fixed",
    right: 0,
    top: 0,
    transform: "translateX(100%)",
    transition: "transform 0.2s ease",
    width: "100%",
    zIndex: 99,

    "&.active": {
      transform: "translateX(0)",
    },

    "& > .preview-header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      flex: "0 1 0",
      gap: "20px",
      minHeight: "70px",
      padding: "0 20px",
      width: "100%",

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      },
    },

    "& .preview-content": {
      flex: "1 1 0",
      overflow: "auto",
      padding: "35px 50px 70px",

      "& .preview-card": {
        backgroundColor: "#FFFFFF",
        border: "1px solid #DEE2EA",
        boxShadow: "2px 4px 8px 5px rgba(0, 0, 0, 0.1)",
        borderRadius: "4px",
        margin: "0 auto 20px",
        maxWidth: "300px",
        padding: "15px",
        height: "max-content",

        "& .media-wrapper": {
          alignItems: "center",
          backgroundColor: "#C4C4C4",
          borderRadius: "4px 4px 0 0",
          display: "flex",
          justifyContent: "center",
          marginBottom: "24px",
          minHeight: "130px",
          overflow: "hidden",
          position: "relative",
          width: "100%",

          "&.doc": {
            backgroundColor: "#F5F5F5",
            color: "#A1AEB4",
            fontSize: "90px",
            fontWeight: 900,
            lineHeight: "96px",
          },

          "&.vdo": {
            "&::after": {
              borderLeft: "32px solid #ffffff",
              borderTop: "16px solid transparent",
              borderBottom: "16px solid transparent",
              content: '""',
              cursor: "pointer",
              display: "block",
              filter: "drop-shadow(0px 5px 2px #00000040)",
              // height: "32px",
              left: "50%",
              top: "50%",
              transform: "translate(-50%,-50%)",
              position: "absolute",
              // width: "32px",
            },

            "&.playing": {
              "&:hover": {
                "&::after": {
                  display: "block",
                },
              },

              "&::after": {
                borderTop: "none",
                borderBottom: "none",
                borderLeft: "12px solid #ffffff",
                borderRight: "12px solid #ffffff",
                content: '""',
                cursor: "pointer",
                display: "none",
                height: "32px",
                left: "50%",
                top: "50%",
                transform: "translate(-50%,-50%)",
                position: "absolute",
                width: "8px",
              },
            },
          },

          "& .image, & .video": {
            objectFit: "cover",
            objectPosition: "center",
            width: "100%",
          },

          "& .video": {
            height: "130px",
          },
        },

        "& .header": {
          color: "#767676",
          fontSize: "16px",
          fontWeight: 700,
          lineHeight: "16px",
          marginBottom: "5px",
          minHeight: "unset!important",
          padding: "0!important",
        },

        "& .body": {
          color: "#767676",
          fontSize: "14px",
          fontWeight: 400,
          lineHeight: "16px",
          marginBottom: "12px",

          "& >": {
            "& *": {
              "&:not(:last-child)": {
                marginBottom: "16px",
              },
            },
          },
        },

        "& .footer": {
          alignItems: "flex-start!important",
          border: "none!important",
          color: "#76767680",
          fontSize: "12px",
          fontWeight: 500,
          lineHeight: "16px",
          marginBottom: "20px",
          minHeight: "unset!important",
          padding: "0!important",
        },

        "& .preview-actions": {
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-between",
          gap: "16px",

          "& .btn-action": {
            backgroundColor: "#ffffff",
            border: "1px solid #DEE2EA",
            borderRadius: "8px",
            color: "#767676",
            flex: "1 1 auto",
            fontSize: "14px",
            fontWeight: 500,
            height: "40px",
            lineHeight: "16px",
            padding: "0 5px",
            textTransform: "initial",
            minWidth: "calc(50% - 8px)",

            "& .label": {
              overflow: "hidden",
              textAlign: "left",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "template-preview": {
      maxWidth: "360px",
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
