import { Fragment, useState } from "react";
import {
  Chip,
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItemText,
  ListItemIcon,
} from "@mui/material";

import { ListItem } from "@mui/material";
import { MoreVert } from "@mui/icons-material";
import { IconButton } from "@material-ui/core";

import {
  deleteTemplateById,
  getTemplatePreview,
} from "../../../../../../api/WhatsappTemplate";
import { useWhatsAppTemplate } from "../../../../../../contexts/WhatsappTemplate";
import { useToastMessage } from "../../../../../../contexts";
import { useWaitingLoader } from "../../../../../../contexts/waitingLoaderContext";
import MessagePreview from "../MessagePreview";
import useStyles from "../styles/muiStyles";

const MessageItem = (props) => {
  const classes = useStyles();
  const { title, description, templateId, template_status } = props;
  const // 3dot menu state
    [anchorEl, setAnchorEl] = useState(null);
  const {
    templatesData,
    setTemplatesData,
    setEditTemplateId,
    setShowDefault,
    setShowPreviewForm,
    setShowTemplateForm,
  } = useWhatsAppTemplate();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const { openLoader, closeLoader } = useWaitingLoader();
  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  //preview state for item
  const [showPreview, togglePreview] = useState(false);
  const [previewData, setPreviewData] = useState("");
  const openPreview = async (templateId) => {
    const res = await getTemplatePreview(templateId);
    if (!res.success) {
      return setErrorMessage(res.message);
    } else {
      setPreviewData(res.payload);
      togglePreview(true);
    }
    handleClose();
  };
  const closePreview = () => {
    togglePreview(false);
  };
  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  //function deleting template
  const deleteTemplate = async (templateId, template_status) => {
    openLoader();
    const res = await deleteTemplateById(templateId);
    handleClose();
    if (!res.success && res.status !== 200) {
      closeLoader();
      return setErrorMessage(res.message);
    } else {
      closeLoader();
      let updatedTemplates = [];
      if (template_status === "DRAFT") {
        updatedTemplates = templatesData.filter(
          (template) => template.id !== templateId
        );
      } else {
        updatedTemplates = templatesData.map((template) => {
          if (template.id === templateId) {
            return { ...template, template_status: "DELETED" };
          } else {
            return { ...template };
          }
        });
      }
      setTemplatesData((previousTemplates) =>
        previousTemplates.filter((template) => templateId !== template.id)
      );
      setShowDefault(true);
      setShowPreviewForm(false);
      setShowTemplateForm(false);
      setEditTemplateId("");
      return setSuccessMessage(res.message);
    }
  };
  const getStatusColor = (template_status) => {
    if (template_status === "DRAFT") {
      return "#FFAD33";
    }
    if (template_status === "APPROVED") {
      return "#25BA00";
    }
    if (template_status === "REJECTED") {
      return "#EB2F05";
    }
    if (template_status === "SUBMITTED") {
      return "#04C4EE";
    }
  };

  return (
    <Fragment>
      <ListItem component="div">
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{title}</h2>
              <Chip
                label={template_status}
                className={"status-chip"}
                size="small"
                style={{
                  backgroundColor: getStatusColor(template_status),
                  alignItems: "center",
                }}
              />
            </div>
          }
          secondary={description}
        />
        <ListItemIcon>
          {template_status !== "DELETED" && (
            <IconButton
              // disabled
              onClick={handleClick}
            >
              <MoreVert />
            </IconButton>
          )}
          {Boolean(anchorEl) && (
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              keepMounted
              MenuListProps={{
                "aria-labelledby": "Options",
              }}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              className={classes["item-options"]}
            >
              {template_status !== "DRAFT" && (
                <MenuItem
                  onClick={() => openPreview(templateId)}
                  style={{ padding: "8px", justifyContent: "center" }}
                >
                  {"Preview"}
                </MenuItem>
              )}
              {template_status === "DRAFT" && (
                <MenuItem
                  onClick={() => {
                    setEditTemplateId(templateId);
                    setShowDefault(false);
                    setShowPreviewForm(false);
                    handleClose();
                  }}
                  style={{ padding: "8px", justifyContent: "center" }}
                >
                  {"Edit"}
                </MenuItem>
              )}

              <MenuItem
                onClick={() => {
                  handleClickOpen();
                  handleClose();
                }}
                style={{ padding: "8px", justifyContent: "center" }}
              >
                {"Delete"}
              </MenuItem>
            </Menu>
          )}
        </ListItemIcon>
      </ListItem>
      {showPreview && previewData && (
        <MessagePreview closePreview={closePreview} previewData={previewData} />
      )}
      {/* <Loader label="Deleting" /> */}
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              deleteTemplate(templateId, template_status);
              handleClickClose();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default MessageItem;
