/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { Fragment, useEffect, useRef, useState } from "react";
import { Button, Link, IconButton } from "@mui/material";
import ReactHtmlParser from "react-html-parser";
import leftArrow from "../../../../../../assets/icons/leftArrow.svg";

import useStyles from "./styles/muiStyles";

interface IPreviewTemplateProps {
  closePreview?: any;
  previewData?: any;
}

const MessagePreview: React.FunctionComponent<IPreviewTemplateProps> = (
  props
) => {
  const { closePreview, previewData } = props,
    classes = useStyles(),
    videoRef = useRef<HTMLVideoElement>(null),
    [videoState, setVideoState] = useState(""),
    [playVideo, setPlayVideo] = useState(false),
    [displayPreview, setDisplayPreview] = useState(false);
  const { components } = previewData;

  let headerText = "";
  let bodyText = "";
  let footerText = "";
  let buttonText = [];
  let file = "";
  let format = "";
  for (const component of components) {
    if (component.type === "HEADER") {
      format = component.format;
      headerText = component.text;

      if (component.format === "TEXT") {
        if (component.example) {
          headerText = headerText.replace(
            "{{1}}",
            component.example.header_text[0]
          );
        }
      } else {
        file = component.example?.header_handle[0];
      }
    }
    if (component.type === "BODY") {
      bodyText = component.text;
      if (component.example) {
        let bodyVar = component.example.body_text[0];
        let counter = 0;
        for (let i = 0; i < bodyVar.length; i++) {
          counter++;
          bodyText = bodyText.replace("{{" + counter + "}}", bodyVar[i]);
        }
      }
    }
    if (component.type === "FOOTER") {
      footerText = component.text;
    }
    if (component.type === "BUTTONS") {
      console.log(component.buttons);
      buttonText = component.buttons;
    }
  }

  const [header, setHeader] = useState(headerText);
  const [body, setBody] = useState(bodyText);
  const [footer, setFooter] = useState(footerText);
  const [buttons, setButtons] = useState(buttonText);

  useEffect(() => {
    setTimeout(() => setDisplayPreview(true), 200);
  }, []);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener("playing", (event) => {
        setVideoState("playing");
      });
      ["pause", "ended"].forEach((eventName: string) =>
        videoRef.current.addEventListener(eventName, (event) =>
          setVideoState("")
        )
      );
      playVideo ? videoRef.current.play() : videoRef.current.pause();
    }
  }, [playVideo]);

  return (
    <Fragment>
      <div className={classes.overlay} />
      <div
        className={`${classes["template-preview"]}${
          displayPreview ? " active" : ""
        }`}
      >
        <div className="preview-header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              setDisplayPreview(false);
              setTimeout(() => closePreview(), 200);
            }}
            sx={{ color: "transparent", p: 0 }}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className="title">{"Preview Template"}</h1>
        </div>
        <div className="preview-content">
          <div className="preview-card">
            {format === "IMAGE" && (
              <div className="media-wrapper img">
                <img alt="card-img" className="image" src={file} />
              </div>
            )}

            {format === "VIDEO" && (
              <Link
                underline="none"
                component="button"
                className={`media-wrapper vdo ${videoState}`}
                onClick={() => setPlayVideo(!playVideo)}
              >
                <video
                  ref={videoRef}
                  className="video"
                  disablePictureInPicture={true}
                >
                  {file && <source src={file} type="video/mp4" />}
                </video>
              </Link>
            )}

            {format === "DOCUMENT" && (
              <Link
                underline="none"
                className={`media-wrapper doc`}
                href={file}
                target="_blank"
              >
                {file ? "PDF" : ""}
              </Link>
            )}

            <div className="header">{header}</div>
            <div className="body">
              <p>
                {ReactHtmlParser(body.replaceAll(/(?:\r\n|\r|\n)/g, "<br />"))}
              </p>
            </div>
            <div className="footer">{footer}</div>
            <div className="preview-actions">
              {buttons &&
                buttons.map((button, index) => {
                  let link = "";
                  if (button.type === "URL") {
                    if (button.example) {
                      link = button.example[0];
                    } else {
                      link = button.url;
                    }
                  }
                  if (button.type === "PHONE_NUMBER") {
                    link = `tel:${button.phone_number}`;
                  }
                  if (button.type === "QUICK_REPLY") {
                    return (
                      <Button
                        key={index}
                        variant="outlined"
                        className="btn-action"
                      >
                        <span className="label">{button.text}</span>
                      </Button>
                    );
                  } else {
                    return (
                      <Button
                        key={index}
                        href={link}
                        variant="outlined"
                        className="btn-action"
                        target="_blank"
                      >
                        {button.text}
                      </Button>
                    );
                  }
                })}
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default MessagePreview;
