/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useCallback, useEffect, useState } from "react";
import { Divider, Button, Link } from "@mui/material";
import { Hidden } from "@material-ui/core";

import { getTemplatesByBusiness } from "../../../../../api/WhatsappTemplate";
import { useWhatsAppTemplate } from "../../../../../contexts/WhatsappTemplate";
import leftArrow from "../../../../../assets/icons/leftArrow.svg";
import inactiveWA from "../../../../../assets/inactive-wa.svg";

import MessageHeader from "./MessageHeader";
import MessageFooter from "./MessageFooter";
import MessageItem from "./MessageItem";

import useStyles from "./styles/muiStyles";

const MessagesContainer = ({ showDefault, newTemplateMessage, waStatus }) => {
  const classes = useStyles();
  const { templatesData, setTemplatesData, setShowDefault } =
    useWhatsAppTemplate();
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(true);

  const preload = useCallback(() => {
    getTemplatesByBusiness()
      .then((res) => {
        if (res.payload) {
          setLoading(false);
          setShowDefault(false);
          setTemplatesData(res.payload);
        }
      })
      .catch((error: any) => console.error(error.message));
  }, []);

  useEffect(() => {
    waStatus && preload();
  }, [waStatus, preload]);

  return (
    <Fragment>
      {/* Mobile View */}
      <Hidden smUp implementation="js">
        <div className={classes["mobile-settings"]}>
          {templatesData?.length === 0 && (
            <div className="header">
              <a href="/business-settings">
                <img alt="back" src={leftArrow} style={{ margin: "0 10px" }} />
              </a>
              <h1 className="title">{"Template Messages"}</h1>
            </div>
          )}
          <div className="content">
            {!waStatus ? (
              <div className="message-container">
                <div className="card-group inactive">
                  <img src={inactiveWA} alt="inactive-wa" />
                  <div className="info">
                    <p className="desc">
                      {
                        "hi Business API account is not active yet. It usually takes upto 72 business hours for activation."
                      }
                    </p>
                    <p className="desc">
                      {"Please contact us on "}
                      <Link
                        href="mailto:support@easysocial.io"
                        underline="hover"
                      >
                        {"support@easysocial.io"}
                      </Link>
                      {" in case it is taking longer than expected."}
                    </p>
                  </div>
                </div>
              </div>
            ) : (
              !loading && (
                <div className="message-container">
                  {templatesData?.length === 0 ? (
                    <div className="default-block">
                      <div className="cta">
                        <Button
                          variant="contained"
                          type="button"
                          className="btn-action"
                          onClick={newTemplateMessage}
                        >
                          {"New Template Message"}
                        </Button>
                      </div>
                      <div className="info">
                        <p>
                          {
                            "In order to initiate a conversation message your customers. Create a new template message. Send a template message to masses from "
                          }
                          <strong>{"Broadcast"}</strong>
                          {" option in "}
                          <strong>{"Engage"}</strong>
                          {" workspace."}
                        </p>
                      </div>
                    </div>
                  ) : (
                    <div className="card-group">
                      <MessageHeader
                        searchQuery={searchQuery}
                        onChange={(e: any) => {
                          setSearchQuery(e.target.value);
                        }}
                        clearQuery={() => setSearchQuery("")}
                        newTemplateMessage={newTemplateMessage}
                        templateCount={templatesData?.length ?? 0}
                      />
                      <Divider className="full" />
                      <div className="card-content">
                        {templatesData &&
                          templatesData
                            .filter((element: any) =>
                              element?.name
                                ?.toLowerCase()
                                .includes(searchQuery?.toLowerCase())
                            )
                            .map((template, index) => (
                              <MessageItem
                                key={index}
                                title={template.name}
                                description={template.description}
                                templateId={template._id ?? template.id}
                                template_status={
                                  template.template_status ??
                                  template.status.toUpperCase()
                                }
                              />
                            ))}
                      </div>
                      {false && (
                        <MessageFooter
                          newTemplateMessage={newTemplateMessage}
                        />
                      )}
                    </div>
                  )}
                </div>
              )
            )}
          </div>
        </div>
      </Hidden>
      {/* Table/Desktop View */}
      <Hidden xsDown implementation="js" only="xs">
        {!waStatus ? (
          <div className={classes["message-container"]}>
            <div className="card-group inactive">
              <img src={inactiveWA} alt="inactive-wa" />
              <div className="info">
                <p className="desc">
                  {
                    "hi Your WhatsApp Business API account is not active yet. It usually takes upto 72 business hours for activation."
                  }
                </p>
                <p className="desc">
                  {"Please contact us on "}
                  <Link href="mailto:support@easysocial.io" underline="hover">
                    {"support@easysocial.io"}
                  </Link>
                  {" in case it is taking longer than expected."}
                </p>
              </div>
            </div>
          </div>
        ) : (
          !loading && (
            <div className={classes["message-container"]}>
              {templatesData?.length === 0 ? (
                <div className="card-group default">
                  <div className="inner-block">
                    <div className="cta">
                      <Button
                        variant="contained"
                        type="button"
                        className="btn-action"
                        onClick={newTemplateMessage}
                      >
                        {"New Template Message"}
                      </Button>
                    </div>
                    <div className="info">
                      <p>
                        {
                          "In order to initiate a conversation message your customers. Create a new template message. Send a template message to masses from "
                        }
                        <strong>{"Broadcast"}</strong>
                        {" option in "}
                        <strong>{"Engage"}</strong>
                        {" workspace."}
                      </p>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="card-group">
                  <MessageHeader
                    searchQuery={searchQuery}
                    onChange={(e: any) => {
                      setSearchQuery(e.target.value);
                    }}
                    clearQuery={() => setSearchQuery("")}
                    newTemplateMessage={newTemplateMessage}
                    templateCount={templatesData?.length ?? 0}
                  />
                  <Divider className="full" />
                  <div className="card-content">
                    {templatesData &&
                      templatesData
                        .filter((element: any) =>
                          element?.name
                            ?.toLowerCase()
                            .includes(searchQuery?.toLowerCase())
                        )
                        .map((template, index) => {
                          return (
                            <MessageItem
                              key={index}
                              title={template.name}
                              description={template.description}
                              templateId={template.id}
                              template_status={
                                template.template_status ??
                                template.status.toUpperCase()
                              }
                            />
                          );
                        })}
                  </div>
                </div>
              )}
            </div>
          )
        )}
      </Hidden>
    </Fragment>
  );
};

export default MessagesContainer;
