import { ChangeEvent, CSSProperties, FC, useState } from "react";
import {
  Card,
  CardContent,
  FormControl,
  Button,
  MenuItem,
} from "@material-ui/core";
import classes from "./QuickReply.module.css";

import { useWhatsAppTemplate } from "../../../../../contexts/WhatsappTemplate";
import { CustomTextFieldFilled } from "../../../../UI/CustomTextField/CustomTextField";
import { Close } from "@mui/icons-material";

interface CallToActionCardProps {
  closeCard?: () => void;
  cardTitle?: string | "Untitled";
  labelValue: string;
  labelName: string;
  type: string;
  phone_text: string;
  phone_number: string;
  index: number;
  url: string;
  onChangeValue?: (
    event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => void;
  style?: CSSProperties;
}

interface CardHeaderProps {
  cardTitle: string;
  closeCard: () => void;
}

const CardHeader: FC<CardHeaderProps> = ({ cardTitle, closeCard }) => {
  return (
    <div className={classes.Header}>
      <h1>{cardTitle || "Card Title"}</h1>

      <div className={classes.Card_Control} onClick={closeCard}>
        <Close />
      </div>
    </div>
  );
};

const CallToActionCard: FC<CallToActionCardProps> = ({
  closeCard,
  cardTitle,
  style,
  onChangeValue,
  labelValue,
  labelName,
  type,
  phone_number,
  url,
  index,
  phone_text,
}) => {
  const { changeCallActionByNameAndValue } = useWhatsAppTemplate(),
    [urlVariable, setUrlVariable] = useState([]),
    [urlValue, setUrlValue] = useState(url ? url : ""),
    actionTypeData = [
      {
        name: "URL",
        value: "URL",
      },
      {
        name: "Phone Number",
        value: "PHONE_NUMBER",
      },
    ],
    addURLVariable = () => {
      if (urlVariable.length > 0) {
        return;
      }
      const newValue = `${urlValue}{{1}}`,
        matches = newValue.match(/[{][{]\d{1,3}[}][}]/gm);
      setUrlVariable((prevUrlVariable) =>
        matches ? matches : prevUrlVariable
      );
      changeCallActionByNameAndValue(index, "url", newValue);
      setUrlValue(newValue);
    },
    fixURLVariable = (event) => {
      const {
        target: { name, value },
      } = event;
      if (urlVariable.length > 0) {
        setUrlValue(value.replace(urlVariable[0], "{{1}}"));
        changeCallActionByNameAndValue(
          index,
          name,
          value.replace(urlVariable[0], "{{1}}")
        );
      }
    },
    changeURLVariable = (event) => {
      const {
          target: { value },
        } = event,
        matches = value.match(/[{][{]\d{1,3}[}][}]/gm);
      setUrlVariable(matches ? matches : []);
      setUrlValue(value);
    };

  return (
    <Card variant="outlined" className={classes.ItemCard} style={style}>
      <CardContent>
        <CardHeader closeCard={closeCard} cardTitle={cardTitle} />
        {/* Select  Item*/}
        <FormControl variant="filled" className={classes.ItemSelect}>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Action Type"
              placeholder="Action Type"
              // value={message.name || ""}
              variant="filled"
              type="select"
              fullWidth
              value={type}
              onChange={onChangeValue}
              required
              /*              onChange={(e) => {
              setActionType(e.target.value);
            }}  */
              inputProps={{ "aria-label": "Without label" }}
              name="type"
            >
              {actionTypeData.map((value, index) => {
                return (
                  <MenuItem value={value.value} key={index}>
                    {value.name}
                  </MenuItem>
                );
              })}
            </CustomTextFieldFilled>
          </div>
          {type !== "" && (
            <div className="field">
              <CustomTextFieldFilled
                label={"Label"}
                onChange={onChangeValue}
                variant="filled"
                value={phone_text}
                name="text"
                required
              />
            </div>
          )}
          {type === "URL" && (
            <div className="field">
              <div className="wrapper">
                <CustomTextFieldFilled
                  label={"URL"}
                  variant="filled"
                  value={urlValue}
                  name="url"
                  onChange={(event) => {
                    changeURLVariable(event);
                    onChangeValue(event);
                  }}
                  onBlur={fixURLVariable}
                  required
                />
              </div>
              <div className="limiter action">
                <Button
                  size="small"
                  focusRipple={false}
                  className="add-variable"
                  onClick={() => addURLVariable()}
                  disabled={urlVariable.length === 1}
                >
                  {"ADD VARIABLE"}
                </Button>
                <span className="limit">{`${urlVariable.length}/1 Variable`}</span>
              </div>
            </div>
          )}
          {type === "PHONE_NUMBER" && (
            <div className="field">
              <CustomTextFieldFilled
                label={"Phone"}
                onChange={onChangeValue}
                variant="filled"
                value={phone_number}
                name="phone_number"
                required
              />
            </div>
          )}
        </FormControl>
      </CardContent>
    </Card>
  );
};

export default CallToActionCard;
