/* eslint-disable no-useless-escape */
import React, { Fragment, FunctionComponent, useEffect, useState } from "react";
import { Button, IconButton } from "@mui/material";
import { MenuItem, Switch } from "@material-ui/core";

import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../../../../Components/UI/CustomTextField/CustomTextField";
import { UIButton } from "../../../../../Components/exports";
import languageData from "../../../../../utils/language.json";
import leftArrow from "../../../../../assets/icons/leftArrow.svg";
import { createTemplate } from "../../../../../api/WhatsappTemplate";
import { useWhatsAppTemplate } from "../../../../../contexts/WhatsappTemplate";
import QuickReply from "../../../../../Components/BusinessSettings/Whatsapp/TemplateMessage/Buttons/QuickReply";
import CallToActionCard from "../../../../../Components/BusinessSettings/Whatsapp/TemplateMessage/Buttons/CallToAction";
import { useToastMessage } from "../../../../../contexts/ToastMessageContext";
import { useWaitingLoader } from "../../../../../contexts/waitingLoaderContext";

import useStyles from "./styles/muiStyles";

interface ITemplateFormProps {}

const TemplateForm: FunctionComponent<ITemplateFormProps> = (props) => {
  const classes = useStyles(),
    { setSuccessMessage, setErrorMessage } = useToastMessage(),
    { openLoader, closeLoader } = useWaitingLoader(),
    [data, setData] = useState({
      templateName: "",
      templateCategory: "",
      language: "",
      templateType: "",
      headerType: "",
      headerMessage: "",
      buttonType: "",
      bodyMessage: "",
      footerMessage: "",
      quickLabel: "",
    }),
    addButton = (e) => {
      e.preventDefault();
      if (!data.buttonType) {
        setErrorMessage("Please Select button Type");
      }
      if (data.buttonType === "QUICK_REPLY") {
        if (quickReplyButton.length < 3) {
          AddQuickReplyButton();
        } else {
          setErrorMessage("Limit exceeded");
        }
      }
      if (data.buttonType === "CALL_TO_ACTION") {
        if (callToActionButton.length < 2) {
          AddCallToActionButton();
        } else {
          setErrorMessage("Limit exceeded");
        }
      }
    },
    onKeyup = (e) => {
      const { name, value } = e.target,
        matches = value.match(/^[A-Za-z0-9_]+$/g),
        fixedValue =
          matches === null
            ? value.slice(0, -1)
            : value.replace(/\s/g, "").toLowerCase();
      e.target.value = fixedValue;
      setData((prevState) => ({
        ...prevState,
        [name]: fixedValue,
      }));
    },
    onHandleChange = (e) => {
      const { name, value } = e.target;
      setData((prevState) => ({
        ...prevState,
        [name]: value,
      }));
    },
    [leftVariable, setLeftVariable] = useState({
      header: [],
      headerVariable: 0,
      body: [],
    }),
    [leftCharacter, setLeftCharacter] = useState({
      headerMessage: 0,
      bodyMessage: 0,
      footerMessage: 0,
    }),
    addHeaderVariable = () => {
      if (leftVariable.header.length > 0) {
        return;
      }
      const newValue = `${data.headerMessage}{{1}}`,
        matches = newValue.match(/[{][{]\d{1,3}[}][}]/gm);
      setData((prevState) => ({
        ...prevState,
        headerMessage: newValue,
      }));
      setLeftCharacter((prevState) => ({
        ...prevState,
        headerMessage: 60 - newValue.length,
      }));
      setLeftVariable((prevState) => ({
        ...prevState,
        header: matches ? [matches[0]] : [],
        headerVariable: matches && matches.length >= 1 ? 1 : 0,
      }));
    },
    fixHeaderVariable = (event) => {
      const { name } = event.target;
      leftVariable.header.length > 0 &&
        setData((prevState) => ({
          ...prevState,
          [name]:
            leftVariable.header.length > 0 &&
            data.headerMessage.replace(leftVariable.header[0], "{{1}}"),
        }));
    },
    leftHeaderVariable = (event) => {
      const { value } = event.target,
        matches = value.match(/[{][{]\d{1,3}[}][}]/gm);
      setLeftVariable((prevState) => ({
        ...prevState,
        header: matches ? [matches[0]] : [],
        headerVariable: matches && matches.length >= 1 ? 1 : 0,
      }));
    },
    leftCharHeaderMessage = (event) => {
      var input = event.target.value;
      const { name, value } = event.target;
      setLeftCharacter((prevState) => ({
        ...prevState,
        headerMessage: 60 - input.length,
      }));
      setData((prevState) => ({
        ...prevState,
        [name]: value.replaceAll(/#|\(|\)|\\|\+|\`|~|\*|\*|\=|\[|\]|\^/g, ""),
      }));
    },
    addBodyVariable = () => {
      const newValue = `${data.bodyMessage}{{${leftVariable.body.length + 1}}}`,
        matches = newValue.match(/[{][{]\d{1,3}[}][}]/gm);
      setData((prevState) => ({
        ...prevState,
        bodyMessage: newValue,
      }));
      setLeftCharacter((prevState) => ({
        ...prevState,
        bodyMessage: 1024 - newValue.length,
      }));
      setLeftVariable((prevState) => ({
        ...prevState,
        body: matches ? matches : [],
      }));
    },
    fixBodyVariable = (event) => {
      if (leftVariable.body.length === 0) {
        return;
      }
      setData((prevState) => ({
        ...prevState,
        bodyMessage: leftVariable.body.reduce(
          (accumulator, currentValue, index) =>
            accumulator.replace(currentValue, `{{${index + 1}}}`),
          data.bodyMessage
        ),
      }));
    },
    leftCharBodyMessage = (event) => {
      const { name, value } = event.target,
        matches = value.match(/[{][{]\d{1,3}[}][}]/gm);
      setLeftVariable((prevState) => ({
        ...prevState,
        body: matches ? matches : [],
      }));
      setLeftCharacter((prevState) => ({
        ...prevState,
        bodyMessage: 1024 - value.length,
      }));
      setData((prevState) => ({
        ...prevState,
        [name]: value,
      }));
    },
    leftCharFooterMessage = (event) => {
      var input = event.target.value;
      const { name, value } = event.target;
      setLeftCharacter((prevState) => ({
        ...prevState,
        footerMessage: 60 - input.length,
      }));
      setData((prevState) => ({
        ...prevState,
        [name]: value.replaceAll(/#|\(|\)|\\|\+|\`|~|\*|\*|\=|\[|\]|\^/g, ""),
      }));
    },
    {
      setShowTemplateForm,
      headerTypeData,
      buttonTypeData,
      templateCategoryData,
      quickReplyButton,
      callToActionButton,
      AddQuickReplyButton,
      AddCallToActionButton,
      removeQuickReply,
      removeCallAction,
      handleChangeQuickReply,
      handleChangeCallAction,
      setNewTemplateData,
      templatesData,
      setTemplatesData,
      setShowPreviewForm,
    } = useWhatsAppTemplate(),
    [showForm, setShowForm] = useState(false),
    //header toggle
    [headerToggle, setHeaderToggle] = useState(false),
    onChangeHeader = (event) => {
      setHeaderToggle(event.target.checked);
    },
    [buttonToggle, setButtonToggle] = useState(false),
    onChangeButton = (event) => {
      setButtonToggle(event.target.checked);
    },
    //end header toggle
    //Footer toggle
    [footerToggle, setFooterToggle] = useState(false),
    onChangeFooter = (event) => {
      setFooterToggle(event.target.checked);
    },
    //end Footer toggle
    submit = async (e) => {
      e.preventDefault();
      openLoader();
      const businessId = localStorage.getItem("businessId");
      const tempData = {
        name: data.templateName.replace(/\s/g, "_").toLowerCase(),
        category: data.templateCategory.replace(/\s/g, "_").toUpperCase(),
        language: data.language,
        template_type: data.templateType,
        businessId,
        components: [
          {
            format: data.headerType,
            text: data.headerMessage,
            type: "HEADER",
          },
          {
            text: data.bodyMessage,
            type: "BODY",
          },
          {
            text: data.footerMessage,
            type: "FOOTER",
          },
          {
            buttons: [...quickReplyButton, ...callToActionButton],
            type: "BUTTONS",
          },
        ],
      };
      const res = await createTemplate(tempData);
      if (!res.success) {
        closeLoader();
        return setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      } else {
        closeLoader();
        setNewTemplateData(res.payload);
        setTemplatesData([res.payload, ...templatesData]);
        setShowPreviewForm(true);
        setShowTemplateForm(false);
        return setSuccessMessage("Created");
      }
    };

  useEffect(() => {
    setTimeout(() => setShowForm(true), 200);
  }, []);

  return (
    <Fragment>
      <div className={classes.overlay} />
      <div
        className={`${classes["template-form"]}${showForm ? " active" : ""}`}
      >
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              setShowForm(false);
              setTimeout(() => setShowTemplateForm(false), 200);
            }}
            style={{ color: "transparent", padding: 0 }}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className="title">{"Create New Template"}</h1>
        </div>
        <form onSubmit={(e) => submit(e)}>
          <div className="field">
            <CustomTextFieldFilled
              type="text"
              label="Template Name"
              placeholder="Template Name"
              name="templateName"
              // onChange={(e) => onHandleChange(e)}
              onKeyUp={(e) => onKeyup(e)}
              required
            />
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              required
              label="Template Category"
              placeholder="Template Category"
              value={data.templateCategory}
              onChange={(e) => onHandleChange(e)}
              variant="filled"
              type="select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
              name="templateCategory"
            >
              {templateCategoryData &&
                templateCategoryData.map((category, index) => {
                  return (
                    <MenuItem key={index} value={category.value}>
                      {category.name}
                    </MenuItem>
                  );
                })}
            </CustomTextFieldFilled>
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Language"
              required
              placeholder="Language"
              value={data.language}
              onChange={(e) => onHandleChange(e)}
              variant="filled"
              type="select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
              name="language"
            >
              {languageData.map((lg, index) => {
                return (
                  <MenuItem value={lg.code} key={index}>
                    {lg.name}
                  </MenuItem>
                );
              })}
            </CustomTextFieldFilled>
          </div>
          <div className="field">
            <CustomTextFieldFilled
              select
              label="Template Type"
              required
              placeholder="Template Type"
              value={data.templateType}
              onChange={(e) => onHandleChange(e)}
              variant="filled"
              type="select"
              inputProps={{ "aria-label": "Without label" }}
              fullWidth
              name="templateType"
            >
              <MenuItem value="text">{"Text only"}</MenuItem>
              <MenuItem value="media">{"Media & Interactive"}</MenuItem>
            </CustomTextFieldFilled>
          </div>
          {data.templateType === "media" && (
            <div className="field flex">
              <h4 className="field-title">{"Header"}</h4>
              <Switch
                classes={{
                  root: classes.root,
                  switchBase: classes.switchBase,
                  thumb: classes.thumb,
                  track: classes.track,
                  checked: classes.checked,
                }}
                onChange={(e) => onChangeHeader(e)}
                checked={headerToggle}
              />
            </div>
          )}
          {data.templateType === "media" && headerToggle && (
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Header Type"
                placeholder="Header Type"
                variant="filled"
                type="select"
                onChange={onHandleChange}
                inputProps={{ "aria-label": "Without label" }}
                fullWidth
                name="headerType"
                value={data.headerType}
              >
                {headerTypeData &&
                  headerTypeData.map((value, index) => {
                    return (
                      <MenuItem key={index} value={value.value}>
                        {value.name}
                      </MenuItem>
                    );
                  })}
              </CustomTextFieldFilled>
            </div>
          )}
          {data.templateType === "media" &&
            headerToggle &&
            data.headerType === "TEXT" && (
              <div className="field">
                <div className="wrapper">
                  <CustomInputField
                    multiline
                    placeholder="Message"
                    rows={5}
                    name="headerMessage"
                    value={data.headerMessage}
                    inputProps={{ maxLength: 60 }}
                    required
                    onChange={(e) => {
                      leftHeaderVariable(e);
                      leftCharHeaderMessage(e);
                    }}
                    onBlur={(e) => {
                      fixHeaderVariable(e);
                    }}
                  />
                  <Button
                    size="small"
                    className="add-variable"
                    disabled={leftVariable.headerVariable === 1}
                    onClick={() => addHeaderVariable()}
                  >
                    {"ADD VARIABLE"}
                  </Button>
                </div>
                <div className="limiter">
                  <span className="limit">
                    {leftCharacter.headerMessage}
                    {"/60 Characters"}
                  </span>{" "}
                  <span className="limit">
                    {leftVariable.headerVariable}
                    {"/1 Variable"}
                  </span>
                </div>
              </div>
            )}
          {data.templateType !== "" && (
            <div className="field flex">
              <h4 className="field-title">{"Body"}</h4>
            </div>
          )}
          {data.templateType !== "" && (
            <div className="field">
              <div className="wrapper">
                <CustomInputField
                  multiline
                  placeholder="Message"
                  value={data.bodyMessage}
                  name="bodyMessage"
                  rows={5}
                  inputProps={{ maxLength: 1024 }}
                  onChange={(e) => leftCharBodyMessage(e)}
                  required
                  onBlur={(e) => {
                    fixBodyVariable(e);
                  }}
                />
                <Button
                  size="small"
                  focusRipple={false}
                  className="add-variable"
                  onClick={() => addBodyVariable()}
                >
                  {"ADD VARIABLE"}
                </Button>
              </div>
              <div className="limiter">
                <span className="limit">
                  {leftCharacter.bodyMessage}
                  {"/1024 Characters"}
                </span>
              </div>
            </div>
          )}
          {data.templateType === "media" && (
            <div className="field flex">
              <h4 className="field-title">{"Footer"}</h4>
              <Switch
                classes={{
                  root: classes.root,
                  switchBase: classes.switchBase,
                  thumb: classes.thumb,
                  track: classes.track,
                  checked: classes.checked,
                }}
                onChange={onChangeFooter}
                checked={footerToggle}
              />
            </div>
          )}
          {data.templateType === "media" && footerToggle && (
            <div className="field">
              <div className="wrapper">
                <CustomInputField
                  multiline
                  placeholder="Message"
                  value={data.footerMessage}
                  name="footerMessage"
                  required
                  rows={5}
                  inputProps={{ maxLength: 60 }}
                  onChange={(e) => leftCharFooterMessage(e)}
                />
              </div>
              <div className="limiter">
                <span className="limit">
                  {leftCharacter.footerMessage}
                  {"/60 Characters"}
                </span>
              </div>
            </div>
          )}
          {data.templateType === "media" && (
            <div className="field flex">
              <h4 className="field-title">{"Buttons"}</h4>
              <Switch
                classes={{
                  root: classes.root,
                  switchBase: classes.switchBase,
                  thumb: classes.thumb,
                  track: classes.track,
                  checked: classes.checked,
                }}
                onChange={onChangeButton}
                checked={buttonToggle}
              />
            </div>
          )}
          {data.templateType === "media" && buttonToggle && (
            <div className="field">
              <div className="field flex">
                <CustomTextFieldFilled
                  select
                  label="Button Type"
                  placeholder="Button Type"
                  value={data.buttonType}
                  onChange={(e) => onHandleChange(e)}
                  variant="filled"
                  type="select"
                  style={{
                    flexGrow: 1,
                  }}
                  inputProps={{ "aria-label": "Without label" }}
                  name="buttonType"
                  disabled={
                    quickReplyButton.length > 0 || callToActionButton.length > 0
                  }
                >
                  {buttonTypeData &&
                    buttonTypeData.map((value, index) => {
                      return (
                        <MenuItem key={index} value={value.name}>
                          {value.label}
                        </MenuItem>
                      );
                    })}
                </CustomTextFieldFilled>
                <Button
                  variant="outlined"
                  className="add-button"
                  onClick={addButton}
                >
                  {"Add a Button"}
                </Button>
              </div>
            </div>
          )}
          {data.templateType === "media" &&
            buttonToggle &&
            quickReplyButton &&
            quickReplyButton.map((data, index) => (
              <QuickReply
                key={index}
                cardTitle={`Quick Reply ${index + 1}`}
                closeCard={() => removeQuickReply(index)}
                value={data.quickLabel}
                onChangeValue={handleChangeQuickReply(index)}
              />
            ))}
          {data.templateType === "media" &&
            buttonToggle &&
            callToActionButton &&
            callToActionButton.map((data, index) => (
              <CallToActionCard
                key={index}
                phone_text={data.text}
                phone_number={data.phone_number}
                url={data.url}
                labelName={data.labelName}
                labelValue={data.labelValue}
                type={data.type}
                index={index}
                cardTitle={`Call to Action ${index + 1}`}
                closeCard={() => removeCallAction(index)}
                onChangeValue={handleChangeCallAction(index)}
              />
            ))}
          <div className="actions">
            {false && (
              <button
                className="cancel-btn"
                onClick={() => setShowTemplateForm(false)}
              >
                {"Cancel"}
              </button>
            )}
            <UIButton className="ms-0">{"Preview & Save"}</UIButton>
          </div>
        </form>
      </div>
    </Fragment>
  );
};

export default TemplateForm;
