import { useRef, useState } from "react";
import { IconButton, Button } from "@material-ui/core";
import { motion } from "framer-motion";
import { Hidden, Menu, MenuItem } from "@mui/material";
import { Search, Clear, MoreVert } from "@mui/icons-material";

import leftArrow from "../../../../../../assets/icons/leftArrow.svg";
import { useWhatsAppTemplate } from "../../../../../../contexts/WhatsappTemplate";
import { useToastMessage } from "../../../../../../contexts/ToastMessageContext";
import {
  syncTemplates,
  getTemplatesByBusiness,
} from "../../../../../../api/WhatsappTemplate";
import useStyles from "../styles/muiStyles";

const MessageHeader = (props) => {
  const classes = useStyles();
  const { setTemplatesData } = useWhatsAppTemplate();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const [isActive, setActive] = useState(false);
  const [isSyncing, setSyncing] = useState(false);
  const {
    onChange,
    searchQuery,
    clearQuery,
    newTemplateMessage,
    templateCount,
  } = props;
  const inputField = useRef(null);
  const [anchorEl, setAnchorEl] = useState(null);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const SearchVariant = {
    open: {
      opacity: 1,
    },
    closed: {
      opacity: 0,
    },
  };
  const transition = { duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96] };
  const syncMessages = async () => {
    try {
      setSyncing(true);
      const res = await syncTemplates();
      if (res.success) {
        const res1 = await getTemplatesByBusiness();
        if (!res1.success) {
          setErrorMessage(res.message);
        } else {
          setTemplatesData(res1.payload);
          setSuccessMessage(res.message);
        }
      } else {
        setErrorMessage(res.message);
      }
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setSyncing(false);
    }
  };

  return (
    <div className="header">
      <Hidden xsDown implementation="js" only="xs">
        <div className="top">
          <Button
            variant="contained"
            type="button"
            className="btn-action"
            onClick={newTemplateMessage}
          >
            {"New Template Message"}
          </Button>
        </div>
      </Hidden>
      <div className="below">
        {!isActive && (
          <div className="left">
            <Hidden smUp implementation="js">
              <a href="/business-settings">
                <img src={leftArrow} style={{ margin: "10px" }} alt="back" />
              </a>
            </Hidden>
            <h2>{`${templateCount > 0 ? `${templateCount} ` : ""}Template ${
              templateCount === 0 || templateCount > 1 ? "Messages" : "Message"
            }`}</h2>
          </div>
        )}
        <div className="middle">
          <motion.div
            animate={isActive ? "open" : "closed"}
            transition={transition}
            variants={SearchVariant}
            className={`search-wrapper${isActive ? " active" : ""}`}
          >
            <div className="search-block">
              <Search className="icon" />
              <input
                onChange={onChange}
                ref={inputField}
                placeholder="Search Template"
                value={searchQuery}
                className="search-input"
              />
              <IconButton
                className="clear"
                onClick={() => {
                  clearQuery();
                  setActive(!isActive);
                }}
              >
                <Clear />
              </IconButton>
            </div>
          </motion.div>
          {/* Search Icon */}
          {!isActive && (
            <IconButton
              className="search-toggle"
              onClick={() => {
                return setActive((prevState) => !prevState);
              }}
            >
              <Search />
            </IconButton>
          )}
        </div>
        {!isActive && (
          <div className="right">
            <Hidden mdUp implementation="js">
              <IconButton
                disabled={isSyncing}
                className={`btn-sync${isSyncing ? " syncing" : ""}`}
                onClick={handleClick}
              >
                {isSyncing ? (
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 14 14"
                    fill="none"
                    xmlns="//www.w3.org/2000/svg"
                  >
                    <path
                      d="M12.537 9.457H9.366C9.18035 9.457 9.0023 9.53075 8.87103 9.66203C8.73975 9.7933 8.666 9.97135 8.666 10.157C8.666 10.3427 8.73975 10.5207 8.87103 10.652C9.0023 10.7832 9.18035 10.857 9.366 10.857H11.046C10.2739 11.6639 9.27808 12.2215 8.18661 12.4582C7.09515 12.6948 5.95785 12.5997 4.92084 12.1851C3.88384 11.7704 2.99451 11.0552 2.36711 10.1312C1.73972 9.20727 1.40292 8.11683 1.4 7C1.4 6.81435 1.32625 6.6363 1.19497 6.50503C1.0637 6.37375 0.885652 6.3 0.7 6.3C0.514348 6.3 0.336301 6.37375 0.205025 6.50503C0.0737498 6.6363 0 6.81435 0 7C0.00370066 8.36696 0.407555 9.70292 1.16172 10.843C1.91589 11.9831 2.98737 12.8775 4.24392 13.4157C5.50047 13.9539 6.88711 14.1124 8.23271 13.8716C9.57831 13.6309 10.824 13.0015 11.816 12.061V13.3C11.816 13.4857 11.8897 13.6637 12.021 13.795C12.1523 13.9263 12.3303 14 12.516 14C12.7017 14 12.8797 13.9263 13.011 13.795C13.1423 13.6637 13.216 13.4857 13.216 13.3V10.15C13.2143 9.96914 13.1426 9.79598 13.016 9.66679C12.8895 9.5376 12.7178 9.46242 12.537 9.457ZM7 0C5.20547 0.00511853 3.48144 0.69924 2.184 1.939V0.7C2.184 0.514348 2.11025 0.336301 1.97897 0.205025C1.8477 0.0737498 1.66965 0 1.484 0C1.29835 0 1.1203 0.0737498 0.989025 0.205025C0.85775 0.336301 0.784 0.514348 0.784 0.7V3.85C0.784 4.03565 0.85775 4.2137 0.989025 4.34497C1.1203 4.47625 1.29835 4.55 1.484 4.55H4.634C4.81965 4.55 4.9977 4.47625 5.12897 4.34497C5.26025 4.2137 5.334 4.03565 5.334 3.85C5.334 3.66435 5.26025 3.4863 5.12897 3.35503C4.9977 3.22375 4.81965 3.15 4.634 3.15H2.954C3.72573 2.34351 4.72086 1.78605 5.81168 1.54918C6.90249 1.3123 8.03921 1.40682 9.07592 1.8206C10.1126 2.23439 11.002 2.94855 11.63 3.87142C12.2579 4.7943 12.5957 5.88377 12.6 7C12.6 7.18565 12.6737 7.3637 12.805 7.49497C12.9363 7.62625 13.1143 7.7 13.3 7.7C13.4857 7.7 13.6637 7.62625 13.795 7.49497C13.9263 7.3637 14 7.18565 14 7C14 6.08075 13.8189 5.17049 13.4672 4.32122C13.1154 3.47194 12.5998 2.70026 11.9497 2.05025C11.2997 1.40024 10.5281 0.884626 9.67878 0.532843C8.8295 0.18106 7.91925 0 7 0Z"
                      fill="#4D4D4D"
                    />
                  </svg>
                ) : (
                  <MoreVert />
                )}
              </IconButton>
              {Boolean(anchorEl) && (
                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                  keepMounted
                  MenuListProps={{
                    "aria-labelledby": "Options",
                  }}
                  anchorOrigin={{
                    vertical: "top",
                    // horizontal: "right",
                  }}
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  className={classes["options"]}
                >
                  <MenuItem
                    onClick={() => {
                      syncMessages();
                      handleClose();
                    }}
                    style={{ padding: "8px", justifyContent: "center" }}
                  >
                    {"Sync Messages"}
                  </MenuItem>
                  <MenuItem
                    onClick={() => {
                      newTemplateMessage();
                      handleClose();
                    }}
                    style={{ padding: "8px", justifyContent: "center" }}
                  >
                    {"Add New Template"}
                  </MenuItem>
                </Menu>
              )}
            </Hidden>
            <Hidden smDown implementation="js" only="sm">
              <Button
                variant="outlined"
                type="button"
                className={`btn-sync${isSyncing ? " syncing" : ""}`}
                disabled={isSyncing}
                onClick={() => syncMessages()}
              >
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 14 14"
                  fill="none"
                  xmlns="//www.w3.org/2000/svg"
                >
                  <path
                    d="M12.537 9.457H9.366C9.18035 9.457 9.0023 9.53075 8.87103 9.66203C8.73975 9.7933 8.666 9.97135 8.666 10.157C8.666 10.3427 8.73975 10.5207 8.87103 10.652C9.0023 10.7832 9.18035 10.857 9.366 10.857H11.046C10.2739 11.6639 9.27808 12.2215 8.18661 12.4582C7.09515 12.6948 5.95785 12.5997 4.92084 12.1851C3.88384 11.7704 2.99451 11.0552 2.36711 10.1312C1.73972 9.20727 1.40292 8.11683 1.4 7C1.4 6.81435 1.32625 6.6363 1.19497 6.50503C1.0637 6.37375 0.885652 6.3 0.7 6.3C0.514348 6.3 0.336301 6.37375 0.205025 6.50503C0.0737498 6.6363 0 6.81435 0 7C0.00370066 8.36696 0.407555 9.70292 1.16172 10.843C1.91589 11.9831 2.98737 12.8775 4.24392 13.4157C5.50047 13.9539 6.88711 14.1124 8.23271 13.8716C9.57831 13.6309 10.824 13.0015 11.816 12.061V13.3C11.816 13.4857 11.8897 13.6637 12.021 13.795C12.1523 13.9263 12.3303 14 12.516 14C12.7017 14 12.8797 13.9263 13.011 13.795C13.1423 13.6637 13.216 13.4857 13.216 13.3V10.15C13.2143 9.96914 13.1426 9.79598 13.016 9.66679C12.8895 9.5376 12.7178 9.46242 12.537 9.457ZM7 0C5.20547 0.00511853 3.48144 0.69924 2.184 1.939V0.7C2.184 0.514348 2.11025 0.336301 1.97897 0.205025C1.8477 0.0737498 1.66965 0 1.484 0C1.29835 0 1.1203 0.0737498 0.989025 0.205025C0.85775 0.336301 0.784 0.514348 0.784 0.7V3.85C0.784 4.03565 0.85775 4.2137 0.989025 4.34497C1.1203 4.47625 1.29835 4.55 1.484 4.55H4.634C4.81965 4.55 4.9977 4.47625 5.12897 4.34497C5.26025 4.2137 5.334 4.03565 5.334 3.85C5.334 3.66435 5.26025 3.4863 5.12897 3.35503C4.9977 3.22375 4.81965 3.15 4.634 3.15H2.954C3.72573 2.34351 4.72086 1.78605 5.81168 1.54918C6.90249 1.3123 8.03921 1.40682 9.07592 1.8206C10.1126 2.23439 11.002 2.94855 11.63 3.87142C12.2579 4.7943 12.5957 5.88377 12.6 7C12.6 7.18565 12.6737 7.3637 12.805 7.49497C12.9363 7.62625 13.1143 7.7 13.3 7.7C13.4857 7.7 13.6637 7.62625 13.795 7.49497C13.9263 7.3637 14 7.18565 14 7C14 6.08075 13.8189 5.17049 13.4672 4.32122C13.1154 3.47194 12.5998 2.70026 11.9497 2.05025C11.2997 1.40024 10.5281 0.884626 9.67878 0.532843C8.8295 0.18106 7.91925 0 7 0Z"
                    fill="#4D4D4D"
                  />
                </svg>
                {isSyncing ? "Syncing..." : "Sync Messages"}
              </Button>
            </Hidden>
          </div>
        )}
      </div>
    </div>
  );
};

export default MessageHeader;
