import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 7,
    margin: -7,
  },

  thumb: {
    width: 24,
    height: 24,
    backgroundColor: "#fff",
    boxShadow: "#000",
  },

  switchBase: {
    color: "#000 !important",
    padding: 7,
  },

  track: {
    borderRadius: 20,
    backgroundColor: "#000 !important",
  },

  checked: {
    "& $thumb": {
      backgroundColor: "#fff !important",
    },
    "& + $track": {
      opacity: "1 !important",
      backgroundColor: "#32C5FF !important",
    },
  },

  focusVisible: {},

  overlay: {
    position: "fixed",
    width: "100%",
    height: "100%",
    left: "0px",
    top: "0px",
    backgroundColor: "#00000080",
    zIndex: 0,
  },

  "template-form": {
    backgroundColor: "#FFFFFF",
    boxShadow: "0px 0px 12px 1px rgba(0, 0, 0, 0.25)",
    display: "flex",
    flexDirection: "column",
    height: "100%",
    position: "fixed",
    right: 0,
    top: 0,
    transform: "translateX(100%)",
    transition: "transform 0.2s ease",
    width: "100%",
    zIndex: 1,

    "&.active": {
      transform: "translateY(0)",
    },

    "& > .header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      flex: "0 1 0",
      gap: "20px",
      minHeight: "70px",
      padding: "0 20px",
      width: "100%",

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      },
    },

    "& form": {
      flex: "1 0 0",
      overflowY: "auto",
      padding: "20px 30px 90px",

      "& .field": {
        marginBottom: "20px",

        "&.flex": {
          display: "flex",
          justifyContent: "space-between",
          gap: "20px",
        },

        "& .wrapper": {
          position: "relative",
          marginBottom: "14px",

          "& .add-variable": {
            color: "#12b0ee",
            bottom: "5px",
            fontSize: "12px",
            fontWeight: 400,
            lineHeight: "14px",
            left: "10px",
            position: "absolute",
          },
        },

        "& .limiter": {
          alignItems: "center",
          display: "flex",
          justifyContent: "flex-end",
          gap: "30px",

          "&.action": {
            justifyContent: "space-between",

            "& .add-variable": {
              color: "#12b0ee",
              fontSize: "12px",
              fontWeight: 400,
              lineHeight: "14px",
            },
          },

          "& .limit": {
            color: "#24272C",
            fontSize: "12px",
            fontWeight: 400,
            lineHeight: "14px",
          },
        },

        "& .field-title": {
          color: "#24272C",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "19px",
        },

        "& .MuiInputBase-root": {
          "&::before, &::after": {
            display: "none",
          },
        },

        "& .MuiInputBase-input": {
          color: "#24272C",
          fontSize: "15px",
          fontWeight: 400,
          lineHeight: "18px",
        },

        "& .MuiInput-root": {
          padding: "16px 14px",

          "&.MuiInput-multiline": {
            paddingBottom: "24px",
          },

          "&:not(.MuiInput-multiline)": {
            height: "52px",

            "&::before, &::after": {
              display: "none",
            },
          },
        },

        "& .MuiFormControl-root": {
          height: "52px",

          "& .MuiInputLabel-filled": {
            transform: "translate(14px, 20px) scale(1)",

            "&.MuiInputLabel-shrink": {
              transform: "translate(14px, 10px) scale(0.75)",
            },
          },
        },

        "& .MuiSelect-select": {
          paddingLeft: "14px",

          "&:focus": {
            backgroundColor: "transparent",
          },

          "& .MuiSelect-iconFilled": {
            color: "#24272C",
            right: "17px",
          },
        },

        "& .add-button": {
          backgroundColor: "#FFFFFF",
          border: "1px solid #DEE2EA",
          borderRadius: "8px",
          boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
          color: "#32C5FF",
          flexShrink: 0,
          fontSize: "14px",
          fontWeight: 500,
          height: "40px",
          lineHeight: "16px",
          margin: "auto",
          textTransform: "initial",
        },
      },

      "& .actions": {
        display: "flex",
        justifyContent: "flex-end",
        gap: "16px",

        "& .cancel-btn": {
          color: "#12B0EE",
          border: "1px solid #12B0EE",
          borderRadius: "8px",
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "template-form": {
      maxWidth: "360px",
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
