/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useCallback, useEffect, useState } from "react";
import { Hidden } from "@material-ui/core";

// import CampaignButton from "../../../../Components/CampaignDashboard/UI/CampaignButton";
import { get_wa_Status } from "api/WhatsApp";
import { useToastMessage } from "contexts/ToastMessageContext";
import MessagesContainer from "Container/BusinessSettings/Whatsapp/Template-messages/MessageContainer";
import PreviewTemplate from "Container/BusinessSettings/Whatsapp/Template-messages/PreviewTemplate";
import TemplateForm from "Container/BusinessSettings/Whatsapp/Template-messages/TemplateForm";
import EditTemplateForm from "Container/BusinessSettings/Whatsapp/Template-messages/TemplateForm/Edit";
import { useWhatsAppTemplate } from "contexts/WhatsappTemplate";

import Layout from "../../Layout";

const TemplateMessages = () => {
  const {
      showTemplateForm,
      setShowTemplateForm,
      createTemplateSuccess,
      editTemplateId,
      showPreviewForm,
      showDefault,
      setShowDefault,
    } = useWhatsAppTemplate(),
    newTemplateMessage = () => {
      setShowTemplateForm(true);
      setShowDefault(false);
    };
  const [waStatus, setWaStatus] = useState();
  const { setErrorMessage } = useToastMessage();
  const getWaStatus = useCallback(async () => {
    try {
      const result = await get_wa_Status();
      setWaStatus(result.success);
    } catch (error) {
      setErrorMessage(error.message);
    }
  }, []);

  useEffect(() => {
    getWaStatus();
  }, [getWaStatus]);

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MessagesContainer
          showDefault={showDefault}
          newTemplateMessage={newTemplateMessage}
          waStatus={waStatus}
        />
        {showTemplateForm && <TemplateForm />}
        {showPreviewForm && <PreviewTemplate />}
        {editTemplateId && <EditTemplateForm />}
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <MessagesContainer
            showDefault={showDefault}
            newTemplateMessage={newTemplateMessage}
            waStatus={waStatus}
          />
          {showTemplateForm && <TemplateForm />}
          {showPreviewForm && <PreviewTemplate />}
          {editTemplateId && <EditTemplateForm />}
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default TemplateMessages;
