import axios from "axios";
import { saveAs } from "file-saver";
import { API, API_V2 } from "../../Backend";
import decryptResponse from "../../Shared/decryptResponse";
import { AuthenticatedHeaders } from "../../utils/ApiHeaders";

export const save_wa_messages = async (data) => {
  try {
    const res = await fetch(`${API}/wa/save-flow`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        businessId: localStorage.getItem("businessId"),
      },
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { error: err.message };
  }
};

export const get_wa_messages = async (number, accessToken) => {
  try {
    const res = await fetch(`${API}/wa/get-chart/${number}/v1`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-Admin-Token": accessToken,
      },
    });
    return decryptResponse({ data: await res.json() });
  } catch (err) {
    return { error: err.message };
  }
};

export const getUtilization = async (from, to, businessId) => {
  try {
    const res = await fetch(
      `${API_V2}/whatsapp-usage/utilization?from=${from.getTime()}&to=${to.getTime()}`,
      {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-BusinessId": businessId || localStorage.getItem("businessId"),
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }
    );
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

