export const downloadMonthDateWiseReport = async (businessId, month) => {
  try {
    const res = await fetch(
      `${API_V2}/whatsapp-usage/download-consumed-report`,
      {
        method: "POST",
        headers: AuthenticatedHeaders(),
        body: JSON.stringify(month),
      }
    );
    return res;
  } catch (err) {
    return { message: err.message };
  }
};

export const downloadReportPdf = async (filename) => {
  try {
    axios
      .get(`${API}/fetch-template-month-pdf/${filename}`, {
        responseType: "blob",
      })
      .then((res) => {
        const pdfBlob = new Blob([res.data], { type: "application/pdf" });
        saveAs(pdfBlob, filename + ".pdf");
      });
  } catch (err) {
    return { message: err.message };
  }
};

